import React, { Component } from 'react';
import style from './carouselChild.module.css';
import marker from '../../../../img/carousel/team/marker.png';

export default class CarouselChild extends Component {
  state = {
    list: this.props.list,
  };

  mouseOut = id => {
    this.setState(prevState => {
      const visible = this.state.list.find(el => el.id === id);
      const visibleWithFlag = { ...visible, visible: false };
      const index = prevState.list.indexOf(visible);
      const newArr = [...prevState.list];
      newArr[index] = visibleWithFlag;
      return { list: newArr };
    });
  };

  mouseOver = id => {
    this.setState(prevState => {
      const visible = this.state.list.find(el => el.id === id);
      const visibleWithFlag = { ...visible, visible: true };
      const index = prevState.list.indexOf(visible);
      const newArr = [...prevState.list];
      newArr[index] = visibleWithFlag;
      return { list: newArr };
    });
  };

  render() {
    const { list } = this.state;
    return (
      <ul className={style.slide}>
        {list.map(pers => (
          <li className={style.slideItem} key={pers.id}>
            <img
              onMouseOut={() => this.mouseOut(pers.id)}
              onFocus={() => this.mouseOut(pers.id)}
              onMouseOver={() => this.mouseOver(pers.id)}
              onBlur={() => this.mouseOver(pers.id)}
              className={style.slideFoto}
              src={pers.fotoDesk}
              alt="Foto"
            />
            {pers.visible && (
              <div className={style.hover}>
                <img className={style.marker} src={marker} alt="marker" />
                <div className={style.wrapperName}>
                  <h3 className={style.slideName}>{pers.name}</h3>
                  <p className={style.slidePosition}>{pers.position}</p>
                </div>
              </div>
            )}
          </li>
        ))}
      </ul>
    );
  }
}
